.problem-mobile {
  padding: 30px 30px;
}

.problem-mobile-title {
  font-size: 30px;
  color: #333;
  line-height: 1.7;
}

.problem-mobile-options {
  padding: 20px 0;
}

.problem-mobile-options-item {
  padding: 8px 0;
  margin-bottom: 30px;
  box-sizing: border-box;
  display: flex;
  align-items: flex-start;
}

.problem-mobile-options-item-desc {
  width: calc(100% - 70px);
  margin-top: -4px;
  margin-left: 20px;
  font-size: 30px;
  line-height: 60px;
  color: #333;
}
