.letter-option-mobile {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 50px;
  border: 1px solid #e9e9e9;
  border-radius: 50%;
  line-height: 30px;
  color: #999;
  font-size: 30px;
  font-weight: 700;
  text-align: center;
}

.letter-option-mobile-checked-box {
  display: inline-block;
  width: 50px;
  height: 50px;
  border: 1px solid transparent;
}

.letter-option-mobile-checked {
  width: 50px;
  height: 50px;
}
