.exam-nav-mobile {
  position: fixed;
  bottom: 0;
  left: 0;
  z-index: 2;
  width: 100%;
  height: 110px;
  background-color: #fff;
}

.exam-nav-mobile > ul {
  width: 100%;
  height: 100%;
  border-top: 1px solid #f0f0f0;
  display: flex;
}

.exam-nav-mobile > ul > li {
  flex: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.exam-nav-mobile-icon {
  width: 48px;
  height: 40px;
}

.exam-nav-mobile-icon-spe {
  width: 45px;
  height: 45px;
}

.exam-nav-mobile > ul > li > span {
  margin-top: 10px;
  font-size: 28px;
  color: #999;
  text-align: center;
}
